import { FC } from 'react';

import {
  SponsorBannerWrapper,
  StyledPrefix,
  StyledSponsorImage,
} from './SponsorBanner.styles';
import { SponsorBannerProps } from './SponsorBanner.types';

/**
 * Renders a sponsor banner with an optional prefix, sponsor logo image, and independent logo.
 */

const SponsorBanner: FC<SponsorBannerProps> = ({
  prefix,
  prefixUrl,
  sponsorImage,
  sponsorUrl,
  indyLogo,
}) => {
  const showPrefixAndImage = !!prefix && !!sponsorImage;
  const showIndyLogo = !!indyLogo;
  return (
    <SponsorBannerWrapper
      $showIndyLogo={showIndyLogo}
      $showPrefixAndImage={showPrefixAndImage}
    >
      {prefix && (
        <StyledPrefix href={prefixUrl} target="_blank">
          {prefix}
        </StyledPrefix>
      )}
      {showIndyLogo && indyLogo}
      {sponsorImage && (
        <StyledSponsorImage>
          {sponsorUrl ? (
            <a href={sponsorUrl} target="_blank" rel="noreferrer">
              {sponsorImage}
            </a>
          ) : (
            sponsorImage
          )}
        </StyledSponsorImage>
      )}
    </SponsorBannerWrapper>
  );
};

export default SponsorBanner;
